.notice {
	width: 100%;
	padding: 50px 0;
	border-top: 2px solid #9d9083;
	border-bottom: 2px solid #9d9083;

	> .inner {
		width: 1180px;
		margin: 0px auto;

		h1 {
			font: bold 40px/1.5 'Noto Sans';
			color: #222222;
			margin-bottom: 20px;
			text-align: left;
			letter-spacing: 6px;
		}
		ul {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			li {
				width: 30%;
				border: 1px solid #9d9083;
				padding: 30px;
				margin: 10px;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.notice {
		width: 100%;
		padding: 0px 40px;
	}
}
