nav {
	width: 280px;
	height: 100vh;
	position: fixed;
	top: 0px;
	left: 0px;
	background: rgba(0, 0, 0, 0.815);
	z-index: 10;
	padding: 40px;
	h1 {
		padding-bottom: 20px;
		a {
			font: bold 24px/1.5 'Noto Sans';
			color: #fff;
		}
	}

	#gnb {
		li {
			text-align: center;
			padding: 10px;
			a {
				font: 18px/1.5 'Noto Sans';
				color: #fff;
			}
			&:hover a {
				color: #999;
			}
		}
	}
}
